.exhibitors{
    @include max-width;
    padding: 7rem;
}

.exhibitors-list{
    margin-bottom: 5.5rem;
}

.exhibitors-top{
    display: flex;
    justify-content: space-between;
    margin-bottom: 2rem;
    .top-title{
        font-family: $font-heading;
        font-size: 2.5rem;
        margin-bottom: 2rem;
    }
}

.top-filter{
    display: flex;
    gap: 2rem;
    select, input{
        font-family: $font-content;
        font-size: 1.1rem;
        border: 2px solid $color-red;
        padding: 0.5rem 0.9rem;
    }
    input[type="submit"]{
        padding: 0.5rem 2rem;
        background-color: $color-red;
        cursor: pointer;
        color: white;
        &:hover{
            background-color: $color-red-dark;
        }
    }
}

.filter-input-wrapper{
    position: relative;
    input{
        padding-right: 3rem;
    }
    .icon-search{
        position: absolute;
        right: 15px;
        top: 10px;
    }
}

.exhibitors-wrapper{
    display: flex;
    flex-wrap: wrap;
    gap: 4rem;
}

.exhibitors-item{
    font-family: $font-content;
    font-size: 1.2rem;
    background-color: $color-red;
    padding: 1.2rem;
    display: flex;
    flex-direction: column;
    color: white;
    border-radius: 5px;
    flex: 1 1 27.25%;
    &:hover{
        background-color:  #361515;
    }
    span:not(:last-child){
        margin-bottom: 10px;
    }
    .item-title{
        font-size: 1.5rem;
        font-weight: bold;
    }

    .item-placement{
        &::before{
            content: "";
            width: 15px;
            height: 15px;
            display: inline-block;
            background-image: url('/storage/assets/rightArrow.svg');
            background-repeat: no-repeat;
            vertical-align: bottom;
            margin-right: 10px;

        }
    }
    .item-country{
        &::before{
            content: "";
            width: 15px;
            height: 15px;
            display: inline-block;
            background-image: url('/storage/assets/location.svg');
            background-repeat: no-repeat;
            vertical-align: bottom;
            margin-right: 10px;

        }
    }
    .item-email{
        &::before{
            content: "";
            width: 15px;
            height: 15px;
            display: inline-block;
            background-image: url('/storage/assets/email.svg');
            background-repeat: no-repeat;
            vertical-align: bottom;
            margin-right: 10px;

        }
    }
    .item-website{
        &::before{
            content: "";
            width: 15px;
            height: 15px;
            display: inline-block;
            background-image: url('/storage/assets/globe.svg');
            background-repeat: no-repeat;
            vertical-align: bottom;
            margin-right: 10px;

        }
    }


    .item-keywords{
        margin-top: 10px;
        span{
            background-color: white;
            border-radius: 30px;
            padding: 3px 10px;
            color: black;
            font-size: 0.9rem;
            &:not(:last-child){
                margin-right: 5px;
            }
        }
    }
}

.exhibitors-pagination{
    display: flex;
    justify-content: center;
    gap: 3rem;
    margin-top: 5rem;
    a, span{
        font-family: $font-content;
        font-size: 1.25rem;
        color: black;
        text-decoration: none;
    }
    a:hover:not(.pagination-next):not(.pagination-previous){
        background-color: $color-red;
        color: white;
        padding: 0.5rem;
    }
    .pagination-previous{
        background-image: url('/storage/assets/next.svg');
        width: 20px;
        height: 20px;
        display: block;
        transform: rotate(180deg);
    }
    .pagination-next{
        background-image: url('/storage/assets/next.svg');
        width: 20px;
        height: 20px;
        display: block;
    }
    .active{
        span{
            color: $color-red;
            text-decoration: underline;
            font-weight: bold;
        }
    }
}


.become-exhibitor{
    .be-title{
        font-family: $font-heading;
        font-size: 2.5rem;
        margin-bottom: 2rem;
    }
}

.be-form{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap:2rem 7rem;
    font-family: $font-content;
    div{
        display:flex;
        flex-direction: column;
        flex: 1;
        label{
            margin-bottom: 0.5rem;
            font-size: 1.25rem;
        }
        input[type="text"], input[type="email"], input[type="tel"], input[type="url"], textarea, select{
            font-family: $font-content;
            padding: 0.8rem 0.9rem;
            font-size: 1.25rem;
            border: 1px solid rgba(86, 33, 33, 0.4);
            box-shadow: 2px 2px 18px -11px rgba(0,0,0,0.3);
            width: 100%;
            box-sizing: border-box;
            resize: none;
            &:focus{
                background-color: $color-cta;

            }
        }
    }

    .form-postal-location, .form-firstname-name{
        display: flex;
        flex-direction: row;
        gap: 0.9rem;
    }


    .selectbox{
        position: relative;
        .overSelect{
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
        }


    }
    .checkboxes{
        display: none;
        border: 1px solid rgba(86, 33, 33, 0.4);
        label{
            display: block;
            padding: 0.8rem 0.9rem;
            &:hover{
                background-color: $color-red-light;
                color: white;
            }
        }
    }


}

.form-informations{
    grid-column: 1/3;
    textarea{
        height: 200px
    }

}


.form-submit{
    grid-column: 2/3;
    width: 100%;
    box-sizing: border-box;
    align-items: flex-end;
    input[type="submit"]{
        padding: 1rem 4rem;
        background-color: $color-red-light;
        border: 1px solid rgba(86, 33, 33, 0.4);
        color: white;
        font-weight: bold;
        width: 200px;
        font-size: 1.15rem;
        cursor: pointer;
        &:hover{
            background-color: $color-red;
            border-color: $color-cta;
            color: white;
        }
    }
}

.checkboxesEl-on{
    display: block !important;
}

// ---------- RESPONSIVE ----------
@media screen and (max-width: 890px){
    .exhibitors-item {
        flex: 1 1 100%;
    }
}

@media screen and (max-width: 1140px){
    .exhibitors-top {
        flex-direction: column;
    }
}

@media screen and (max-width: 1000px){
    .be-form {
        grid-gap: 2rem 3rem;
    }
}

@media screen and (max-width: 850px){
    .top-filter {
        display: flex;
        flex-wrap: wrap;

    }
}

@media screen and (max-width: 770px){
    .be-form {
        display: flex;
        flex-direction: column;
        }
}

@media screen and (max-width: 700px){
    .exhibitors {
        padding: 2rem;
    }
}

